.dark{
    background-color:  gray;
    color: white;
}

.container{
    width: 15em;
    margin: 0 0.2em 1em 0.2em;
    float: left;
    position: relative;
}
.container.darkMode{
    color: white;
}
.container.lightMode{
    color: black;
}
.pic img{
    width: 100%;
}
.title{
    font-weight: bold;
    margin-bottom: 1em;
    height: 3em;
    
}
.channel, .views{
    font-size: small;
   
    margin-bottom: 0.2em;
}
.views span{
    padding: 0 0.5em;
}
.close{
    position: absolute;
    right: 0;
}
.edit{
    position: absolute;
    right: 4em;
}